export default {
	components: {},
	data() {
		return {
			coupon: [{
					url: "/pages/brand/index/id/1",
					money: "150",
					title: "满2000减150元",
					ticket: "YMC_5c929fbf47235",
					seller_name: "百达翡丽官方旗舰店",
					end_time: "2019-04-20 01:51:20",
					state: "1"
				},
				{
					url: "/pages/brand/index/id/1",
					money: "50",
					title: "满1000减50元",
					ticket: "YMC_5c929fbf47235",
					seller_name: "百达翡丽官方旗舰店",
					end_time: "2019-04-20 01:51:20",
					state: "1"
				}
			]
		}
	},
	onLoad() {
		this.getPageData()
	},
	methods: {
		goProduct(type){
			console.log(type)
			if(type == '报名'){
				uni.redirectTo({
					'url':'/pages/search/select'
				})
			}else{
				uni.redirectTo({
					'url':'/pages/category/category'
				})
			}
		},
		getPageData() {
			var _this = this;
			this.$http.post('/api/coupons/myCoupons', {}).then(res => {
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var data = res.data.data
					_this.coupon = data
				}
			}).catch(err => {
				console.log(err);
			})
		},
	}
}
